// Subscriptions API v1
// GET /api/v1/revenue-engine/subscriptions - List subscriptions
// POST /api/v1/revenue-engine/subscriptions - Create new subscription

import { NextRequest, NextResponse } from 'next/server'
import { mockSubscriptions, filterBySubAccount } from '@/lib/revenue-engine/mock-data'

// GET - List subscriptions
export async function GET(request: NextRequest) {
  try {
    const { searchParams } = new URL(request.url)
    const subAccountId = searchParams.get('subAccountId')
    const status = searchParams.get('status')
    const page = parseInt(searchParams.get('page') || '1')
    const limit = parseInt(searchParams.get('limit') || '50')
    
    if (!subAccountId) {
      return NextResponse.json(
        { success: false, error: 'subAccountId is required' },
        { status: 400 }
      )
    }
    
    let subscriptions = filterBySubAccount(mockSubscriptions, subAccountId)
    
    if (status && status !== 'undefined' && status !== 'all') {
      subscriptions = subscriptions.filter(s => s.status === status)
    }
    
    const stats = {
      total: subscriptions.length,
      active: subscriptions.filter(s => s.status === 'active').length,
      cancelled: subscriptions.filter(s => s.status === 'cancelled').length,
      pastDue: subscriptions.filter(s => s.status === 'past_due').length,
      mrr: subscriptions.filter(s => s.status === 'active').reduce((sum, s) => sum + s.amount, 0),
      arr: subscriptions.filter(s => s.status === 'active').reduce((sum, s) => sum + s.amount, 0) * 12,
    }
    
    return NextResponse.json({
      success: true,
      data: {
        subscriptions,
        stats,
        pagination: { page, limit, total: subscriptions.length, totalPages: Math.ceil(subscriptions.length / limit) },
      },
    })
  } catch (error: any) {
    console.error('GET /api/v1/revenue-engine/subscriptions error:', error)
    return NextResponse.json({ success: false, error: error.message || 'Failed to fetch subscriptions' }, { status: 500 })
  }
}

// POST - Create new subscription
export async function POST(request: NextRequest) {
  try {
    const body = await request.json()
    const { subAccountId, contactId, contactName, productId, productName, amount, billingCycle } = body
    
    if (!subAccountId || !contactName || !productName || !amount) {
      return NextResponse.json(
        { success: false, error: 'subAccountId, contactName, productName, and amount are required' },
        { status: 400 }
      )
    }
    
    const subscription = {
      id: `sub-${Date.now()}`,
      subscriptionNumber: `SUB-${new Date().getFullYear()}-${String(Date.now()).slice(-5)}`,
      subAccountId,
      contactId,
      contactName,
      productId,
      productName,
      status: 'active',
      billingCycle: billingCycle || 'monthly',
      amount,
      currency: 'USD',
      nextBillingDate: new Date(Date.now() + 30 * 24 * 60 * 60 * 1000).toISOString().split('T')[0],
      startDate: new Date().toISOString().split('T')[0],
      createdAt: new Date().toISOString(),
    }
    
    return NextResponse.json({
      success: true,
      data: { subscription },
      message: 'Subscription created (mock data)',
    }, { status: 201 })
  } catch (error: any) {
    console.error('POST /api/v1/revenue-engine/subscriptions error:', error)
    return NextResponse.json({ success: false, error: error.message || 'Failed to create subscription' }, { status: 500 })
  }
}
